import { read } from 'fs';
import * as xlsx from 'xlsx'

let workbook;
console.log('listern', xlsx)


document.addEventListener("dragover", function (event) {
    event.preventDefault();
});


document.querySelector('#file')!.addEventListener('change', (ev) => {
    const file = (ev.target as HTMLInputElement)?.files?.[0];
    afterFile(file)
})

document.addEventListener('drop', (ev) => {
    ev.preventDefault();
    console.log(ev, 'ev');
    const file = ev.dataTransfer?.files[0];
    console.log(ev)
    if (file) {
        afterFile(file)
    }
})

const afterFile = (file) => {
    const reader = new FileReader();
    reader.readAsArrayBuffer(file)
    reader.onload = () => {
        workbook = xlsx.read(reader.result)
        processFile(workbook)

    }
}
const processFile = (workbook: xlsx.WorkBook) => {
    const newWorkbook = xlsx.utils.book_new();

    const sheet = workbook.Sheets[workbook.SheetNames[0]]
    console.log(sheet, sheet['!merges'])
    const data = xlsx.utils.sheet_to_json<any>(sheet, { header: 1 })
    console.log(data)
    const title = data.shift();
    const headers = data.shift();


    //    
    const start = headers.indexOf('')
    const middle = headers.indexOf('')
    const end = headers.indexOf('');

    const sheetsToNewList = [];

    for (let arr of data) {
        if (!arr || arr.length === 0) {
            continue
        }
        let sum = 0;
        for (let i = start; i < middle; i++) {
            if (typeof arr[i] === 'number') {
                sum += arr[i]
            }
        }
        if (arr[middle] !== sum) {
            arr[middle] = sum;
        }
        for (let i = middle + 1; i < end; i++) {
            if (typeof arr[i] === 'number') {
                arr[middle] -= arr[i]
            }
        }
        if (arr[end] !== sum) {
            arr[end] = sum;
        }

        const userSheet: any[][] = [
            [],
            []
        ];
        for (let i = 0; i < headers.length; i++) {
            if (arr[i] !== undefined) {
                userSheet[0].push(headers[i])
                userSheet[1].push(arr[i])
            }
        }
        sheetsToNewList.push(userSheet)

    }
    const newSheet = xlsx.utils.aoa_to_sheet([
        title.concat(Array.from(headers).fill('').filter((_, index) => index !== 2)),
        headers,
        ...data
    ])

    // 
    newSheet['!merges'] = sheet['!merges']
    newSheet.A1.s = { alignment: { wrapText: true, vertical: 'center', horizontal: 'center' } };
    xlsx.utils.book_append_sheet(newWorkbook, newSheet, '')

    // 

    sheetsToNewList.forEach((sheet,) => {
        const newSheet = xlsx.utils.aoa_to_sheet(sheet)
        xlsx.utils.book_append_sheet(newWorkbook, newSheet, `${sheet[1][0]}`)
    });

    xlsx.writeFile(newWorkbook, '.xlsx')
}
